<template>
  <div>
      <heade-top signin-up="home">
        <span slot="logo" class="head_logo">ele.me</span>
      </heade-top>
      <nav class="city_nav">
          <div class="city_tip">
            <span class="">当前定位城市：</span>
            <span>定位不准请在城市中选择</span>
          </div>
          <router-link :to="'/city/'+guessCityid" class="guess_city">
            <span>{{guessCity}}</span>
              <svg class="arrow_right">
                 <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
              </svg>
          </router-link>
        </nav>
        <section id="hot_city_container">
            <h4 class="city_title">热门城市</h4>
            <ul class="clear citylistul">
              <router-link tag="li" v-for="item in hodcity" :to="'/city/'+item.id" :key="item.id">
              <!-- :key提高循环效率 -->
                {{item.name}}
              </router-link>
            </ul>
        </section>
        <section class="group_city_container">
          <ul class="letter_classify">
            <li v-for="(value,key,index) in sortgroupcity()" :key="key" class="letter_classify_li">
              <h4 class="city_title">
                {{key}}
                <span v-if="index==0">(按字母循序排序)</span>
              </h4>
              <ul class="groupcity_name_container citylistul clear">
                <router-link tag="li" v-for="item in value" :to="'/city/'+item.id" :key="item.id" class="ellipsis">
                    {{item.name}}
                </router-link>
              </ul>
            </li>
          </ul>
        </section>
  </div>
</template>

<script>
import {cityGuess,hotcity,groupcity} from '../../service/getData'
import headeTop from '../../components/header/header'
export default {
  data(){
    return{
      guessCity:'',//当前城市
      guessCityid:'',//当前城市id
      hodcity:[],//热门城市列表
      groupcity:{}//所有城市列表
    }
  },components: {
    headeTop
  },mounted () {
    //我的位置
   cityGuess().then(res=>{
     this.guessCity=res.name;
     this.guessCityid=res.id
   })
   //热门城市
   hotcity().then(res=>{
     this.hodcity=res;
   })
   //所有城市
   groupcity().then(res=>{
     this.groupcity=res;
   })
  },methods: {
    sortgroupcity()
    {
      let sortobj={};
      for(let i=65;i<=90;i++)
      {
        if(this.groupcity[ String.fromCodePoint(i)])
        {
          //String.fromCodePoint()出入一个62-90的数字返回一个字母
          sortobj[String.fromCodePoint(i)]=this.groupcity[String.fromCodePoint(i)];
        }
      }
      return sortobj;
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/mixin.scss';
.head_logo{
  left:0.4rem;
  @include sc(0.71rem,#fff);
  @include wh(2.3rem,0.7rem);
  @include ct;
}
.city_nav{
  padding-top: 2.35rem;
  border:1px solid $bc;
  background-color: #fff;
  margin-bottom: 0.4rem;
  .city_tip{
    @include fj;
    line-height: 1.45rem;
    padding: 0 0.45rem;
    span:nth-of-type(1)
    {
      @include sc(0.55rem,#666);

    }
    span:nth-of-type(2)
    {
      @include sc(0.475rem,#9f9f9f);
      font-weight: 900;
    }
  }
    .guess_city{
    @include fj;
    align-items: center;//垂直居中
     height:1.8rem;
     padding: 0 0.45rem;
     border:1px solid $bc;
     @include font(0.75rem,1.8rem);
     span:nth-of-type(1){
       color:$blue;
     }
     .arrow_right{
       @include wh(.6rem,.6rem);
       fill:#999//fill的CSS属性填写SVG图形的颜色。
     }
  }
}
#hot_city_container{
  background-color: #fff;
  margin-bottom: 0.4rem;
}
.citylistul{
  li{
    float: left;
    text-align: center;
    color:$blue;
    border-bottom: 0.025rem solid $bc;
    border-right: 0.025rem solid $bc;
    @include wh(25%,1.75rem);
    @include font(0.6rem,1.75rem);
    li:nth-of-type(4n)
    {
      border-right: none
    }
  }
}
.city_title{
  color:#666;
  font-weight: 400;
  border-bottom: 1px solid $bc;
  text-indent: 0.45rem;//字体边距
  border-top: 2px solid $bc;
  @include font(0.55rem,1.45rem,'Helvetica Neue');
  span{
    @include sc(0.475rem,#999);
  }
}
  .letter_classify_li{
    margin-bottom: 0.4rem;
    border-bottom: 1px solid $bc;
    background-color: #fff;
    .groupcity_name_container{
            li{
                color: #666;
            }
        }
  }
</style>



